<template>
    <main-nav />
    <router-view v-slot="{ Component }">
        <transition name="fade" mode="out-in">
            <component :is="Component" class="content" />
        </transition>
    </router-view>
</template>
<script>
import MainNav from '@/components/MainNav.vue'
export default {
    name: 'app',
    data() {
        return {}
    },
    created() {
        if (!localStorage.getItem('melonade_device_id')) {
            var d = Date.now()
            var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
                var r = (d + Math.random() * 16) % 16 | 0
                d = d / 16 | 0
                return (c === 'x' ? r : (r & 0x7 | 0x8)).toString(16)
            })
            localStorage.setItem('melonade_device_id', uuid)
        }
    },
    components: {
        MainNav
    },
    computed: {}
}
</script>
<style lang="scss">
.content {
    min-height: 100vh;
}

.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.25s ease;
}

.fade-enter-from,
.fade-leave-to {
    opacity: 0;
}


// General Styling
// imports
@import url('https://fonts.googleapis.com/css?family=Sacramento|Fredericka+the+Great|Quicksand');

// general 
* {
    margin: 0;
    padding: 0;
    font-family: 'Quicksand', sans-serif;
    font-size: 1rem;
    font-style: normal;
    font-variant: normal;
    color: inherit;
    box-sizing: border-box;
}

body {
    height: 100vh;
    width: 100vw;
    overflow-y: overlay;
    background-color: black;
}

span {
    flex: 99 1 auto;
}

a {
    text-decoration: none;
}

// flex and grid classes 
.f-center {
    justify-content: center;
    align-items: center;
}

.f-col {
    display: flex;
    flex-direction: column;
}

.f-grow {
    flex: 99 1 auto;
}

.f-row {
    display: flex;
    flex-direction: row;
}

.f-wrap {
    flex-wrap: wrap;
}

.g-center {
    place-self: center;
}

.g-col-first {
    grid-column: 1;
    grid-row: 1 / -1;
}

.g-col-last {
    grid-column: -1;
    grid-row: 1 / -1;
}

// margin + padding
$directions: ('T': 'top',
'B': 'bottom',
'L': 'left',
'R': 'right'
);
$sizes: ('s': .33rem,
'm': .66rem,
'l': 1rem,
'xl': 4rem,
'xxl': 8rem,
);

@each $sizeName,
$sizeVal in $sizes {
    .m-#{$sizeName} {
        margin: $sizeVal
    }

    .p-#{$sizeName} {
        margin: $sizeVal
    }

    @each $directionShort,
    $directionLong in $directions {
        .m#{$directionShort}-#{$sizeName} {
            margin-#{$directionLong}: $sizeVal
        }

        .p#{$directionShort}-#{$sizeName} {
            padding-#{$directionLong}: $sizeVal
        }
    }
}

// scrollbar
::-webkit-scrollbar {
    width: 5px;
}

::-webkit-scrollbar-track {
    background-color: transparent;
}

::-webkit-scrollbar-thumb {
    background-color: rgb(black, .3);
    border-radius: 20px;
}
</style>