import { createStore } from 'vuex'

export default createStore({
  state: {
    mainNav: {
      compact: false,
      dim: false,
    }
  },
  getters: {
    mainNavState: state => {
      return state.mainNav
    }
  },
  mutations: {
    setMainNavDim (state, bool) {
      state.mainNav.dim = bool
    },
    setMainNavCompact (state, val) {
      state.mainNav.compact = payload
    }
  },
  actions: {
  },
  modules: {
  }
})